
.side_notif {
  position: absolute;
  height: auto;
  width: auto;
  bottom: 10px;
  right: 10px;
  z-index: 3;
}
#notif {
  -webkit-animation-delay: 20s;
          animation-delay: 20s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.macth_extra {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 999999;
  margin: 0;
}
.macth_extra .title {
  margin-top: 5%;
}
.macth_extra .pictures div {
  width: 250px;
  height: 250px;
  border-radius: 100%;
  border: 5px solid #ff5252;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}




