
.profile_background {
    width: 100%;
    height: 250px;
    background-size: cover;
    background-position: center;
}
.profile_data {
    position: absolute;
    width: 350px;
    height: 220px;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    padding: 25px;
    background: rgba(0, 0, 0, 0.6);
}
.profile_data .btn {
    width: 100%;
    text-align: left;
    margin-bottom: 10px;
}
.profile_online {
    width: 13px;
    height: 13px;
    background: green;
    display: inline-block;
    border-radius: 100%;
}
.profile_pictures {
    max-height: 150px;
    max-width: 100%;
    border: 2px solid #e0e0e0;
    border-radius: 2px;
    margin: 10px;
    cursor: pointer;
}
.profile_pictures:hover {
    border: 2px solid #bdbdbd;
}
.nav_profile {
    width: 100%;
    height: 45px;
    position: relative;
    float: left;
    background: red;
    margin: 0;
    padding: 0;
}
.nav_profile .btn {
    margin: 0;
    position: relative;
    float: left;
    width: 25%;
    height: 45px;
    line-height: 45px;
    border-radius: 0;
    display: inline-block;
}
.profile_card {
    margin: 0 !important;
}
.profile_circle {
    width: 180px;
    height: 180px;
    border: 2px solid white;
    border-radius: 4px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 100%;
    position: absolute;
    top: 0;
    left: 20px;
    bottom: 0;
    margin: auto;
}
.profile_card-content .profile_card-title {
    margin-bottom:0
}
td {
    padding-left: 25px;
    padding-right: 25px;
}
.btn-marge {
    margin: 5px;
}
.simplert__content--radius {
    border-radius: 0;
}
strong {
    font-weight: bold;
}
.small_icone {
  font-size: 20px;
}
.alertPicture {
  width: 100%;
  padding: 20px 20px;
  text-align: center;
  background: #DB0B32;
  color: white;
}



