
body {
    font-family: Helvetica, sans-serif;
}
.input-tag {
    display: inline-block;
    height: 32px !important;
    padding: 0 12px !important;
    border-radius: 16px !important;
    background-color: #e4e4e4 !important;
    margin-bottom: 5px !important;
    margin-right: 5px !important;
    border: none !important;
}
.input-tag span {

    font-size: 13px !important;
    font-weight: 500 !important;
    color: rgba(0, 0, 0, 0.6) !important;
    line-height: 32px !important;
}
.input-tag .remove {
    color: grey !important;
}
.remove:hover {
    color: darkgray !important;
}
.vue-input-tag-wrapper {
    border: none !important;
}
.tab {
    cursor: pointer;
}
.biography_tabs {
    width: 350px;
    height: 40px;
    padding: 0;
    list-style-type: none;
    margin: 0;
    background: #f5f5f5;
}
.biography_tabs li
{
    margin: 0;
    display: inline-block;
    width: auto;
    height: 40px;
    cursor: pointer;
    line-height: 45px;
    padding: 5px 15px;
}
.biography_tabs li:hover {
    background: #ff5252;
    color: #fff;
}
.biography_tabs ul
{
    padding: 0;
    list-style: none;
    position: absolute;
    left: auto;
    margin: 0;
    display: none;
    z-index: 10;
    background-color: #ff5252;
    margin-top: -18px;
    width: 80px;
}
.biography_tabs ul li {
    height: 40px;
    line-height: 30px;
}
.biography_tabs li:hover ul
{
    display: block;
}
.biography_tabs ul li:hover
{
    background-color: #333;
}
strong {
    font-weight: bold;
}



